import { Navigate, useRoutes } from 'react-router-dom';
// layouts
import DashboardLayout from './layouts/dashboard';
import LogoOnlyLayout from './layouts/LogoOnlyLayout';
//
import Login from './pages/Login';
import Register from './pages/Register';
import DashboardApp from './pages/DashboardApp';
import Products from './pages/Products';
import Blog from './pages/Blog';
import User from './pages/User';
import NotFound from './pages/Page404';
import Notifications from './pages/Notifications/Notifications';
import Payments from './pages/Payments/Payments';
import Services from './pages/Services/Services';
import NewService from './pages/Services/NewService';
import UpdateService from './pages/Services/UpdateService';
import ServiceProvider from './pages/ServiceProvider/ServiceProvider';
import AddServiceProvider from './pages/ServiceProvider/AddServiceProvider';
import UpdateServiceProvider from './pages/ServiceProvider/UpdateServiceProvider';
import ProviderServices from './components/_dashboard/ServiceProvider/ProvdierServices/ProviderServices';

// ----------------------------------------------------------------------

export default function Router() {
  return useRoutes([
    {
      path: '/dashboard',
      element: <DashboardLayout />,
      children: [
        { element: <Navigate to="/dashboard/app" replace /> },
        { path: 'app', element: <DashboardApp /> },
        { path: 'user', element: <User /> },
        { path: 'notifications', element: <Notifications /> },
        { path: 'payments', element: <Payments /> },
        { path: 'services', element: <Services /> },
        { path: 'new-services', element: <NewService /> },
        { path: 'update-service', element: <UpdateService /> },
        { path: 'service-provider', element: <ServiceProvider /> },
        { path: 'add-service-provider', element: <AddServiceProvider /> },
        { path: 'update-service-provider', element: <UpdateServiceProvider /> },
        { path: 'provider-services', element: <ProviderServices /> },
        { path: 'products', element: <Products /> },
        { path: 'blog', element: <Blog /> }
      ]
    },
    {
      path: '/',
      element: <LogoOnlyLayout />,
      children: [
        { path: 'login', element: <Login /> },
        { path: 'register', element: <Register /> },
        { path: '404', element: <NotFound /> },
        { path: '/', element: <Navigate to="/dashboard" /> },
        { path: '*', element: <Navigate to="/404" /> }
      ]
    },
    { path: '*', element: <Navigate to="/404" replace /> }
  ]);
}
