const Availability = ({ availalbility, id }) => {
  availalbility?.map((day, index) => {
    if (!day.isSelected) {
      day.startTime = 'To';
      day.endTime = 'From';
    }

    return day;
  });
  const defaultDays = availalbility || [
    {
      day: 'Monday',
      endTime: 'From',
      startTime: 'To',
      isSelected: false
    },
    {
      day: 'Tuesday',
      endTime: 'From',
      startTime: 'To',
      isSelected: false
    },
    {
      day: 'Wednesday',
      endTime: 'From',
      startTime: 'To',
      isSelected: false
    },
    {
      day: 'Thursday',
      endTime: 'From',
      startTime: 'To',
      isSelected: false
    },
    {
      day: 'Friday',
      endTime: 'From',
      startTime: 'To',
      isSelected: false
    },
    {
      day: 'Saturday',
      endTime: 'From',
      startTime: 'To',
      isSelected: false
    },
    {
      day: 'Sunday',
      endTime: 'From',
      startTime: 'To',
      isSelected: false
    }
  ];
  const [days, setDays] = useState(defaultDays);
  const [change, setChange] = useState(0);
  const [loading, setLoading] = useState(false);

  const handleStartChange = (e, day) => {
    const delimiter = ':';
    const startTime = e.toLocaleTimeString().split(delimiter).slice(0, 2).join(delimiter);
    const startTimePeriod = e
      .toLocaleTimeString()
      .split(delimiter)
      .slice(2)
      .join(delimiter)
      .slice(-2)
      .toLowerCase();

    const foundIndex = days.findIndex((x) => x.day === day);
    days[foundIndex].startTime = `${startTime} ${startTimePeriod}`;
    setChange(change + 1);
  };

  const handleEndChange = (e, day) => {
    const delimiter = ':';
    const endTime = e.toLocaleTimeString().split(delimiter).slice(0, 2).join(delimiter);
    const endTimePeriod = e
      .toLocaleTimeString()
      .split(delimiter)
      .slice(2)
      .join(delimiter)
      .slice(-2)
      .toLowerCase();

    const foundIndex = days.findIndex((x) => x.day === day);
    days[foundIndex].endTime = `${endTime} ${endTimePeriod}`;
    setChange(change + 1);
  };
  const handleDaySelection = (day) => {
    const foundIndex = days.findIndex((x) => x.day === day);
    if (days[foundIndex].isSelected === true) {
      days[foundIndex].isSelected = false;
    } else {
      days[foundIndex].isSelected = true;
    }
    setChange(change + 1);
  };
  const convertTime12to24 = (time12h) => moment(time12h, 'hh:mm A').format('HH:mm');
  const onSubmit = async () => {
    for (let i = 0; i < days.length; i += 1) {
      if (
        days[i].isSelected &&
        days[i].startTime !== 'To' &&
        convertTime12to24(days[i].startTime) >= convertTime12to24(days[i].endTime)
      ) {
        return NotificationManager.error(
          `Start Time must be greater than end Time of ${days[i].day}`
        );
      }
      console.log();
      if (days[i].isSelected && days[i].startTime === 'To') {
        return NotificationManager.error(`Please enter start time of ${days[i].day}`);
      }
      if (days[i].isSelected && days[i].endTime === 'From') {
        return NotificationManager.error(`Please enter end time of ${days[i].day}`);
      }
    }

    try {
      setLoading(true);
      const data = {
        availalbility: days,
        objectId: id
      };
      const response = await updateProviderAvailibilityTimes(data);
      if (response) {
        setLoading(false);
        Swal.fire({
          position: 'center',
          icon: 'success',
          title: 'availalbility Updated Successfully',
          showConfirmButton: false,
          timer: 2000
        });
      }
    } catch (error) {
      return NotificationContainer.error(`Error! ${error.message}`);
    }
  };
  return (
    <>
      <NotificationContainer />
      {days.map((element, index) => (
        <Stack direction={{ xs: 'column', sm: 'row' }} spacing={2} padding="5px" key={index}>
          <Card
            sx={{
              backgroundColor: element.isSelected ? '#c39586' : '#fff',
              width: '100%',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'space-between',
              padding: '1%',
              color: element.isSelected ? '#fff' : '#c1c5c4'
            }}
          >
            <Typography variant="body2">{element.day}</Typography>
            <Button sx={{ color: element.isSelected && '#fff' }}>
              <Typography variant="body2" onClick={() => handleDaySelection(element.day)}>
                {element.isSelected ? 'Remove' : 'Add'}
              </Typography>
            </Button>
          </Card>
          {element.isSelected && (
            <LocalizationProvider dateAdapter={AdapterDateFns}>
              <TimePicker
                label="Start Time"
                value={element.isSelected ? moment(element.startTime, ['h:mm A']).format() : ''}
                onChange={(e) => handleStartChange(e, element.day)}
                renderInput={(params) => <TextField {...params} />}
              />
              <TimePicker
                label="End Time"
                value={element.isSelected ? moment(element.endTime, ['h:mm A']).format() : ''}
                onChange={(e) => handleEndChange(e, element.day)}
                renderInput={(params) => <TextField {...params} />}
              />
            </LocalizationProvider>
          )}
        </Stack>
      ))}
      <LoadingButton
        size="large"
        type="submit"
        variant="contained"
        loading={loading}
        onClick={() => onSubmit()}
      >
        Save
      </LoadingButton>
    </>
  );
};

const About = ({ props, id }) => {
  const { description, providerWebUri, location } = props;
  const navigate = useNavigate();
  const [isLoading, setIsLoading] = useState(false);

  const RegisterSchema = Yup.object().shape({});

  const formik = useFormik({
    initialValues: {
      studioLocation: location,
      website: providerWebUri,
      description
    },
    validationSchema: RegisterSchema,
    onSubmit: async (values) => {
      setIsLoading(true);
      const { studioLocation, website, description } = values;
      const data = {
        description: description || '',
        webLink: website || '',
        location: studioLocation || '',
        objectId: id
      };
      console.log(data);
      try {
        const response = await updateProviderInfo(data);
        if (response) {
          setIsLoading(false);
          return Swal.fire({
            position: 'center',
            icon: 'success',
            title: 'About updated Successfully',
            showConfirmButton: false,
            timer: 2000
          });
        }
        return NotificationManager.error(`Error ! some thing went wrong`);
      } catch (error) {
        return NotificationManager.error(`Error ! ${error.message}`);
      }
    }
  });

  const {
    errors,
    touched,
    handleSubmit,
    isSubmitting,
    getFieldProps,
    setFieldValue,
    setFieldTouched
  } = formik;
  return (
    <FormikProvider value={formik}>
      <NotificationContainer />

      <Form autoComplete="off" noValidate onSubmit={handleSubmit}>
        <Stack spacing={3}>
          <TextField
            fullWidth
            multiline
            minRows={3}
            maxRows={3}
            label="Description"
            {...getFieldProps('description')}
            error={Boolean(touched.description && errors.description)}
            helperText={touched.description && errors.description}
          />
          <Stack direction={{ xs: 'column', sm: 'row' }} spacing={2}>
            <TextField
              fullWidth
              label="Website"
              {...getFieldProps('website')}
              error={Boolean(touched.website && errors.website)}
              helperText={touched.website && errors.website}
            />
            <TextField
              fullWidth
              label="Studio Location"
              {...getFieldProps('studioLocation')}
              error={Boolean(touched.studioLocation && errors.studioLocation)}
              helperText={touched.studioLocation && errors.studioLocation}
            />
          </Stack>

          <LoadingButton
            fullWidth
            size="large"
            type="submit"
            variant="contained"
            loading={isLoading}
          >
            Save
          </LoadingButton>
        </Stack>
      </Form>
    </FormikProvider>
  );
};

const Gallery = ({ images, id }) => {
  console.log(images);
  const navigate = useNavigate();
  const [Images, setImages] = useState(images || []);
  const [previewImages, setpreviewImages] = useState(images || []);
  const [count, setCount] = useState(0);

  const updateProviderImage = async (data) => {
    const response = await updateProviderImages(data);
    if (response) {
      Swal.fire({
        position: 'center',
        icon: 'success',
        title: 'Service Images Updated Successfully',
        showConfirmButton: false,
        timer: 2000
      });
      navigate('/dashboard/service-provider', { replace: true });
    }
  };

  const handle = () => {
    setCount(count + 1);
  };
  const handleFileUpload = async (e, index) => {
    const image = e.target.files[0];
    const { size } = e.target.files[0];
    if (size / 1024 > 10480) {
      return NotificationManager.error('File size is greater than 10 mb');
    }
    const reader = new FileReader();
    reader.onloadend = async function () {
      const imageFile = new Parse.File(`serviceImg${id}.jpeg`, { base64: reader.result });
      const fileUploadResponse = await imageFile.save();
      const { _url } = fileUploadResponse;
      if (index) {
        Images[index] = _url;
      } else {
        Images[0] = _url;
      }
      const data = {
        images: Images,
        objectId: id
      };
      updateProviderImage(data);
    };
    reader.readAsDataURL(image);
    const downloadUrl = Object.entries(e.target.files).map((e) => URL.createObjectURL(e[1]));
    previewImages[index] = downloadUrl;

    setpreviewImages(previewImages);
    handle();
  };

  const deleteFile = async (index) => {
    const imges = previewImages.filter((e, indx) => indx !== index);
    console.log(imges);
    setpreviewImages(imges);
    setImages(imges);
    handle();

    const data = {
      images: imges,
      objectId: id
    };
    updateProviderImage(data);
  };

  const loop = [0, 1, 2, 3, 4, 5];
  return (
    <>
      <NotificationContainer />
      <Grid container spacing={2}>
        {loop.map((item, index) => (
          <Grid item xs={4} sm={4} md={3}>
            {!previewImages || !previewImages[item] ? (
              <Card>
                <Box sx={{ pt: '100%' }}>
                  <Button
                    variant="contained"
                    component="label"
                    style={{ backgroundColor: 'rgba(255,255,255,0.10)' }}
                    sx={{ position: 'absolute', top: '40%', left: '32%' }}
                    onChange={(e) => handleFileUpload(e, index)}
                  >
                    <Icon
                      icon="ant-design:camera-outlined"
                      width={50}
                      style={{ color: '#c39586' }}
                    />
                    <input type="file" hidden fullWidth accept="image/*" />
                  </Button>
                </Box>
              </Card>
            ) : (
              <>
                <Card>
                  <Box sx={{ pt: '100%' }}>
                    <ProductImageStyle alt="Image" src={previewImages[item]} />
                    <Icon
                      icon="ci:close-big"
                      width={20}
                      style={{
                        cursor: 'pointer',
                        position: 'absolute',
                        top: 5,
                        right: 5
                      }}
                      onClick={() => deleteFile(index)}
                    />
                  </Box>
                </Card>
              </>
            )}
          </Grid>
        ))}
      </Grid>
    </>
  );
};
