import * as Yup from 'yup';
import Parse from 'parse/dist/parse.min';
import { useState } from 'react';
import { Icon } from '@iconify/react';
import { useFormik, Form, FormikProvider } from 'formik';
import { useNavigate } from 'react-router-dom';
import eyeFill from '@iconify/icons-eva/eye-fill';
import eyeOffFill from '@iconify/icons-eva/eye-off-fill';
// material
import { Stack, TextField, Card, Grid, Button, IconButton, InputAdornment } from '@mui/material';
import { styled } from '@mui/material/styles';
import { LoadingButton } from '@mui/lab';
import Swal from 'sweetalert2';
import InputMask from 'react-input-mask';
import { NotificationContainer, NotificationManager } from 'react-notifications';
import DateTimePicker from '@mui/lab/DateTimePicker';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import { Box } from '@mui/system';
import { encode, decode } from 'js-base64';
import { addService, addServiceProvider } from '../../../utils/CloudFunctions';
// ----------------------------------------------------------------------

const ProductImageStyle = styled('img')({
  top: 0,
  width: '100%',
  height: '100%',
  objectFit: 'cover',
  position: 'absolute'
});

export default function ServiceProviderForm() {
  const navigate = useNavigate();
  const [image, setImage] = useState();
  const [isLoading, setIsLoading] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const [file, setFile] = useState([]);
  const [base64, setBase64] = useState();
  const [formValues, setFormValues] = useState([{ category: '' }]);

  const handleShowPassword = () => {
    setShowPassword((show) => !show);
  };

  const handleChange = (i, e) => {
    const newFormValues = [...formValues];
    newFormValues[i].category = e.target.value;
    setFormValues(newFormValues);
  };

  const addFormFields = () => {
    setFormValues([...formValues, { category: '' }]);
  };

  const removeFormFields = (i) => {
    const newFormValues = [...formValues];
    newFormValues.splice(i, 1);
    setFormValues(newFormValues);
  };

  const handleFileUpload = async (e) => {
    const image = e.target.files[0];
    const { size } = e.target.files[0];
    if (size / 1024 > 10480) {
      return NotificationManager.error('File size is greater than 10 mb');
    }
    const reader = new FileReader();
    reader.onloadend = function () {
      setBase64(reader.result);
    };
    reader.readAsDataURL(image);
    const downloadUrl = Object.entries(e.target.files).map((e) => URL.createObjectURL(e[1]));
    setFile([downloadUrl]);
    setImage(image);
  };

  const RegisterSchema = Yup.object().shape({
    studioName: Yup.string()
      .min(2, 'Too Short!')
      .max(50, 'Too Long!')
      .required('Studio name is required'),
    password: Yup.string().min(8, 'Too Short!').required('Password is required'),
    email: Yup.string().email('Enter a valid email').required('Email is required'),
    contact: Yup.string().required('Phone number is required')
  });

  const formik = useFormik({
    initialValues: {
      studioName: '',
      email: '',
      password: '',
      contact: '',
      website: '',
      description: ''
    },
    validationSchema: RegisterSchema,
    onSubmit: async (values) => {
      setIsLoading(true);
      const { studioName, email, password, contact, website, description } = values;
      const data = {
        providerName: studioName,
        providerEmail: email,
        providerPhone: contact,
        providerWebUri: website,
        description,
        password
      };
      try {
        const response = await addServiceProvider(data);
        if (response) {
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: 'Service Provider Added Successfully',
            showConfirmButton: false,
            timer: 2000
          });
          return navigate('/dashboard/service-provider', { replace: true });
        }
        return NotificationManager.error(`Error ! some thing went wrong`);
      } catch (error) {
        return NotificationManager.error(`Error ! ${error.message}`);
      }
    }
  });

  const {
    errors,
    touched,
    handleSubmit,
    isSubmitting,
    getFieldProps,
    setFieldValue,
    setFieldTouched
  } = formik;

  return (
    <FormikProvider value={formik}>
      <NotificationContainer />
      <Form autoComplete="off" noValidate onSubmit={handleSubmit}>
        <Stack spacing={3}>
          <Stack direction={{ xs: 'column', sm: 'row' }} spacing={2}>
            <TextField
              fullWidth
              label="Studio Name"
              {...getFieldProps('studioName')}
              error={Boolean(touched.studioName && errors.studioName)}
              helperText={touched.studioName && errors.studioName}
            />
            <TextField
              fullWidth
              label="Email"
              {...getFieldProps('email')}
              error={Boolean(touched.email && errors.email)}
              helperText={touched.email && errors.email}
            />
          </Stack>
          <Stack direction={{ xs: 'column', sm: 'row' }} spacing={2}>
            <InputMask
              mask="99 999 9999"
              {...getFieldProps('contact')}
              disabled={false}
              maskChar=" "
            >
              {() => (
                <TextField
                  fullWidth
                  label="Phone Number"
                  {...getFieldProps('contact')}
                  error={Boolean(touched.contact && errors.contact)}
                  helperText={touched.contact && errors.contact}
                />
              )}
            </InputMask>
            <TextField
              fullWidth
              autoComplete="current-password"
              type={showPassword ? 'text' : 'password'}
              label="Password"
              {...getFieldProps('password')}
              InputProps={{
                endAdornment: (
                  <InputAdornment position="end">
                    <IconButton onClick={handleShowPassword} edge="end">
                      <Icon icon={showPassword ? eyeFill : eyeOffFill} />
                    </IconButton>
                  </InputAdornment>
                )
              }}
              error={Boolean(touched.password && errors.password)}
              helperText={touched.password && errors.password}
            />
            <TextField
              fullWidth
              label="Website"
              {...getFieldProps('website')}
              error={Boolean(touched.website && errors.website)}
              helperText={touched.website && errors.website}
            />
          </Stack>
          <TextField
            fullWidth
            multiline
            minRows={3}
            maxRows={3}
            label="Description"
            {...getFieldProps('description')}
            error={Boolean(touched.description && errors.description)}
            helperText={touched.description && errors.description}
          />

          {/* <TextField
            fullWidth
            autoComplete="current-password"
            type={showPassword ? 'text' : 'password'}
            label="Password"
            {...getFieldProps('password')}
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton edge="end" onClick={() => setShowPassword((prev) => !prev)}>
                    <Icon icon={showPassword ? eyeFill : eyeOffFill} />
                  </IconButton>
                </InputAdornment>
              )
            }}
            error={Boolean(touched.password && errors.password)}
            helperText={touched.password && errors.password}
          /> */}

          <LoadingButton
            fullWidth
            size="large"
            type="submit"
            variant="contained"
            loading={isLoading}
          >
            Add Service Provider
          </LoadingButton>
        </Stack>
      </Form>
    </FormikProvider>
  );
}
