import { useEffect, useState } from 'react';
import { Icon } from '@iconify/react';
import appleFilled from '@iconify/icons-ant-design/apple-filled';
// material
import { alpha, styled } from '@mui/material/styles';
import { Card, Typography, CircularProgress } from '@mui/material';
// utils
import { fShortenNumber } from '../../../utils/formatNumber';
import { getServiceProvider } from '../../../utils/CloudFunctions';

// ----------------------------------------------------------------------

const RootStyle = styled(Card)(({ theme }) => ({
  boxShadow: 'none',
  textAlign: 'center',
  padding: theme.spacing(5, 0),
  color: theme.palette.info.darker,
  backgroundColor: theme.palette.info.lighter
}));

const IconWrapperStyle = styled('div')(({ theme }) => ({
  margin: 'auto',
  display: 'flex',
  borderRadius: '50%',
  alignItems: 'center',
  width: theme.spacing(8),
  height: theme.spacing(8),
  justifyContent: 'center',
  marginBottom: theme.spacing(3),
  color: theme.palette.info.dark,
  backgroundImage: `linear-gradient(135deg, ${alpha(theme.palette.info.dark, 0)} 0%, ${alpha(
    theme.palette.info.dark,
    0.24
  )} 100%)`
}));

// ----------------------------------------------------------------------

const TOTAL = 1352831;

export default function AppNewUsers() {
  const [TOTAL, setTOTAL] = useState();
  useEffect(async () => {
    const users = await getServiceProvider();
    if (users.length > 0) {
      return setTOTAL(users.length);
    }
    setTOTAL('Not found');
  }, []);
  return (
    <RootStyle>
      <IconWrapperStyle>
        <Icon icon="bx:bxs-user-detail" width={22} height={22} />
      </IconWrapperStyle>
      <Typography variant="h3">
        {(() => {
          if (!TOTAL) return <CircularProgress style={{ color: '#04297A' }} />;
          if (TOTAL === 'Not found') return fShortenNumber(0);
          return fShortenNumber(TOTAL);
        })()}
      </Typography>
      <Typography variant="subtitle2" sx={{ opacity: 0.72 }}>
        Service Providers
      </Typography>
    </RootStyle>
  );
}
