import { useState, useEffect } from 'react';
// material
import { Stack, styled, Container, Button, Card, TextField } from '@mui/material';

import * as React from 'react';
import PropTypes, { element } from 'prop-types';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import { useLocation, useNavigate } from 'react-router-dom';
import { NotificationContainer, NotificationManager } from 'react-notifications';
import { LoadingButton } from '@mui/lab';
import TimePicker from '@mui/lab/TimePicker';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import moment from 'moment';
import Swal from 'sweetalert2';
import _ from 'lodash';
import { getServices, updateProviderService } from '../../../../utils/CloudFunctions';
import Page from '../../../Page';

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired
};

const RootStyle = styled(Page)(({ theme }) => ({
  [theme.breakpoints.up('md')]: {
    display: 'flex'
  }
}));

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`
  };
}

export default function ProviderServices() {
  const location = useLocation();
  const navigate = useNavigate();
  const { services, index, Uid, id } = location.state;
  const [allServices, setAllServices] = useState([]);
  const [loading, setLoading] = useState(true);
  const [isloading, setIsLoading] = useState(false);

  useEffect(async () => {
    const response = await getServices();
    const data = [];
    response.forEach(async (service) => {
      const categories = [];
      service.attributes.categories.forEach((item) => {
        categories.push({
          categoryName: item,
          isSelected: false,
          price: '0',
          serviceName: service.serviceName
        });
      });

      data.push({
        objectId: service.id,
        imageUrl: service.attributes.imageUrl,
        serviceName: service.attributes.serviceName,
        categories
      });
      let result;
      if (services) {
        result = _(allServices).differenceBy(services, 'objectId').concat(services).value();
        setAllServices(result);
      } else {
        setAllServices(data);
      }
    });

    setLoading(false);
  }, []);
  const [value, setValue] = useState(index ?? 0);
  const handleChanges = (event, newValue) => {
    setValue(newValue);
  };
  if (loading) {
    return 'Loading.....';
  }

  const onsubmit = async () => {
    let error = false;
    allServices.forEach((item) => {
      item.categories.forEach((element) => {
        if (element.isSelected && (!element.price || element.price <= 0)) {
          error = true;
        }
      });
    });

    if (error) {
      return NotificationManager.error(`Price greater than 0 `);
    }
    setIsLoading(true);
    const data = {
      services: allServices,
      objectId: id
    };

    try {
      const response = await updateProviderService(data);
      if (response) {
        Swal.fire({
          position: 'center',
          icon: 'success',
          title: 'Services Updated Successfully',
          showConfirmButton: false,
          timer: 2000
        });
        return navigate('/dashboard/service-provider', { replace: true });
      }
      return NotificationManager.error(`Error! Some thing went wrong`);
    } catch (error) {
      setIsLoading(false);
      return NotificationManager.error(`Error! ${element.categoryName}`);
    }
  };
  return (
    <RootStyle title="Provider Service | MakeMeUp">
      <Container>
        <Stack direction="row" alignItems="center" justifyContent="space-between" mb={5}>
          <Typography variant="h4" gutterBottom>
            All Services
          </Typography>
        </Stack>
        <Box sx={{ bgcolor: 'background.paper' }}>
          <Tabs
            value={value}
            onChange={handleChanges}
            variant="scrollable"
            aria-label="full width tabs example"
          >
            {allServices.map((value, index) => (
              <Tab label={value.serviceName} {...a11yProps(index)} />
            ))}
          </Tabs>
          {allServices.map((item, index) => (
            <TabPanel value={value} index={index}>
              {item.categories.map((cat, index) => (
                <SubCatogroyCard
                  category={cat}
                  setAllServices={setAllServices}
                  allServices={allServices}
                  index={index}
                  item={item}
                />
              ))}
              <LoadingButton
                size="large"
                type="submit"
                variant="contained"
                loading={isloading}
                onClick={() => onsubmit()}
              >
                Save
              </LoadingButton>
            </TabPanel>
          ))}
        </Box>
      </Container>
    </RootStyle>
  );
}

const SubCatogroyCard = ({ category, item, setAllServices, allServices, index }) => {
  const [price, setPrice] = useState(category.price);
  const [count, setCount] = useState(0);
  const handleChanges = (e) => {
    setPrice(e.target.value);
    const Serviceindex = allServices.findIndex((x) => x.objectId === item.objectId);

    allServices[Serviceindex].categories[index].price = e.target.value;
    setAllServices(allServices);
  };
  const handleIsSelected = () => {
    const Serviceindex = allServices.findIndex((x) => x.objectId === item.objectId);
    if (allServices[Serviceindex].categories[index].isSelected) {
      allServices[Serviceindex].categories[index].isSelected = false;
    } else {
      allServices[Serviceindex].categories[index].isSelected = true;
    }
    setAllServices(allServices);
    setCount(count + 1);
  };

  return (
    <>
      <NotificationContainer />

      <Stack direction={{ xs: 'column', sm: 'row' }} spacing={2} padding="5px">
        <Card
          sx={{
            backgroundColor: category.isSelected ? '#c39586' : '#fff',
            width: '100%',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'space-between',
            padding: '1%',
            color: category.isSelected ? '#fff' : '#c1c5c4'
          }}
        >
          <Typography constiant="body2">{category.categoryName}</Typography>
          <Button sx={{ color: category.isSelected && '#fff' }} onClick={() => handleIsSelected()}>
            <Typography variant="body2">{category.isSelected ? 'Remove' : 'Add'}</Typography>
          </Button>
        </Card>
        {category.isSelected && (
          <TextField label="SAR" value={price} type="number" onChange={handleChanges} />
        )}
      </Stack>
    </>
  );
};
