import Parse from 'parse/dist/parse.min';

export const getUsers = () => Parse.Cloud.run('getUserList');
export const getNotifications = () => Parse.Cloud.run('fetchNotifications');
export const getPayments = () => Parse.Cloud.run('fetchPayments');
export const getServices = () => Parse.Cloud.run('servicesList');
export const addService = (data) => Parse.Cloud.run('addService', data);
export const updateService = (data) => Parse.Cloud.run('updateService', data);
export const deleteservice = (data) => Parse.Cloud.run('deleteservice', data);
export const getServiceProvider = () => Parse.Cloud.run('getServiceProviders');
export const addServiceProvider = (data) => Parse.Cloud.run('addServiceProvider', data);
export const deleteserviceProviders = (data) => Parse.Cloud.run('deleteserviceProviders', data);
export const updateProviderAvailibilityTimes = (data) =>
  Parse.Cloud.run('updateProviderAvailibilityTimes', data);
export const updateProviderInfo = (data) => Parse.Cloud.run('updateProviderInfo', data);
export const updateProviderImages = (data) => Parse.Cloud.run('updateProviderImages', data);
export const updateProviderService = (data) => Parse.Cloud.run('updateProviderService', data);
export const LoginAdmin = async (username, password) => {
  const loggedInUser = await Parse.User.logIn(username, password);
  return Parse.User.current();
};
export const LogOutAdmin = () => Parse.User.logOut();
