import * as Yup from 'yup';
import Parse from 'parse/dist/parse.min';
import { useState } from 'react';
import { Icon } from '@iconify/react';
import { useFormik, Form, FormikProvider } from 'formik';
import { useNavigate } from 'react-router-dom';
// material
import { Stack, TextField, Card, Grid, Button } from '@mui/material';
import { styled } from '@mui/material/styles';
import { LoadingButton } from '@mui/lab';
import Swal from 'sweetalert2';
import { NotificationContainer, NotificationManager } from 'react-notifications';
import DateTimePicker from '@mui/lab/DateTimePicker';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import { Box } from '@mui/system';
import { encode, decode } from 'js-base64';
import { updateService } from '../../../utils/CloudFunctions';
// ----------------------------------------------------------------------

const ProductImageStyle = styled('img')({
  top: 0,
  width: '100%',
  height: '100%',
  objectFit: 'cover',
  position: 'absolute'
});

const convertIntoObjects = (categories) => {
  const res = categories.reduce((accum, item, i) => {
    if (i === categories.length) return accum;
    accum.push({ category: item });
    return accum;
  }, []);
  return res;
};

export default function UpdateServiceForm({ props }) {
  const navigate = useNavigate();
  const { id, categories, name, avatarUrl } = props;
  const [image, setImage] = useState();
  const [isLoading, setIsLoading] = useState(false);
  const [file, setFile] = useState(avatarUrl);
  const [base64, setBase64] = useState();
  const cat = convertIntoObjects(categories);
  const [formValues, setFormValues] = useState(cat ?? [{ name: '' }]);

  const handleChange = (i, e) => {
    const newFormValues = [...formValues];
    newFormValues[i].category = e.target.value;
    setFormValues(newFormValues);
  };

  const addFormFields = () => {
    setFormValues([...formValues, { category: '' }]);
  };

  const removeFormFields = (i) => {
    const newFormValues = [...formValues];
    newFormValues.splice(i, 1);
    setFormValues(newFormValues);
  };

  const handleFileUpload = async (e) => {
    const image = e.target.files[0];
    const { size } = e.target.files[0];
    if (size / 1024 > 10480) {
      return NotificationManager.error('File size is greater than 10 mb');
    }
    const reader = new FileReader();
    reader.onloadend = function () {
      setBase64(reader.result);
    };
    reader.readAsDataURL(image);
    const downloadUrl = Object.entries(e.target.files).map((e) => URL.createObjectURL(e[1]));
    setFile([downloadUrl]);
    setImage(image);
  };

  const RegisterSchema = Yup.object().shape({
    serviceName: Yup.string()
      .min(2, 'Too Short!')
      .max(50, 'Too Long!')
      .required('Service name is required')
  });

  const formik = useFormik({
    initialValues: {
      serviceName: name
    },
    validationSchema: RegisterSchema,
    onSubmit: async (values) => {
      setIsLoading(true);
      const { serviceName } = values;
      const categories = formValues.map((obj) => obj.category);
      try {
        let imageFile;
        let data;
        data = {
          objectId: id,
          serviceName,
          imageUrl: avatarUrl,
          categories
        };
        if (base64) {
          imageFile = new Parse.File('serviceImg.jpeg', { base64 });
          const fileUploadResponse = await imageFile.save();
          const { _url } = fileUploadResponse;
          data = {
            objectId: id,
            serviceName,
            imageUrl: _url,
            categories
          };
        }
        if (categories[0] === '' || categories[0] === undefined) {
          data = {
            objectId: id,
            serviceName,
            imageUrl: avatarUrl,
            categories: undefined
          };
        }
        const response = await updateService(data);
        if (response) {
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: 'Service Update Successfully',
            showConfirmButton: false,
            timer: 2000
          });
          return navigate('/dashboard/services', { replace: true });
        }
        setIsLoading(false);
        return NotificationManager.error(`Error ! some thing went wrong`);
      } catch (error) {
        return NotificationManager.error(`Error ! ${error.message}`);
      }
    }
  });

  const {
    errors,
    touched,
    handleSubmit,
    isSubmitting,
    getFieldProps,
    setFieldValue,
    setFieldTouched
  } = formik;

  return (
    <FormikProvider value={formik}>
      <NotificationContainer />
      <Form autoComplete="off" noValidate onSubmit={handleSubmit}>
        <Stack spacing={3}>
          <Stack direction={{ xs: 'column', sm: 'row' }} spacing={2}>
            <TextField
              fullWidth
              label="Service Name"
              {...getFieldProps('serviceName')}
              error={Boolean(touched.serviceName && errors.serviceName)}
              helperText={touched.serviceName && errors.serviceName}
            />
          </Stack>

          {formValues &&
            formValues.map((element, index) => (
              <Stack direction={{ xs: 'column', sm: 'row' }} spacing={2}>
                <TextField
                  fullWidth
                  label="Add Category"
                  name="name"
                  value={element.category}
                  onChange={(e) => handleChange(index, e)}
                  error={Boolean(touched.category && errors.category)}
                  helperText={touched.category && errors.category}
                />
                {index ? (
                  <Button
                    type="button"
                    className="button remove"
                    onClick={() => removeFormFields(index)}
                  >
                    Remove
                  </Button>
                ) : (
                  <Button
                    className="button add"
                    type="button"
                    onClick={() => addFormFields(`category${index}`)}
                  >
                    Add
                  </Button>
                )}
              </Stack>
            ))}

          <Grid container spacing={2}>
            {file && (
              <Grid item xs={4} sm={4} md={2}>
                <Card style={{}}>
                  <Box sx={{ pt: '100%', position: 'relative' }}>
                    <ProductImageStyle alt="Image" src={file} />
                  </Box>
                </Card>
              </Grid>
            )}
          </Grid>
          <Stack direction={{ xs: 'column', sm: 'row' }} spacing={2}>
            <Button
              variant="contained"
              component="label"
              style={{ backgroundColor: 'rgba(255,255,255,0.5)' }}
            >
              <Icon icon="ant-design:camera-outlined" width={50} style={{ color: '#c39586' }} />
              <input
                type="file"
                hidden
                fullWidth
                accept="image/*"
                onChange={handleFileUpload}
                error={Boolean(touched.photo && errors.photo)}
                helperText={touched.photo && errors.photo}
              />
            </Button>
          </Stack>

          <LoadingButton
            fullWidth
            size="large"
            type="submit"
            variant="contained"
            loading={isLoading}
          >
            Update Service
          </LoadingButton>
        </Stack>
      </Form>
    </FormikProvider>
  );
}
